import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {_adminGetTravelList} from "../../../../redux/actions";
import {Avatar, Button, Card, Image,Tag, Table,Badge} from "antd";
import AvatarStatus from "../../../../components/shared-components/AvatarStatus";
import {EyeOutlined, LoadingOutlined,EditOutlined} from "@ant-design/icons";
import Flex from "../../../../components/shared-components/Flex";
import AvatarCustom from "../../../../components/shared-components/avatar-custom";
import { Link } from 'react-router-dom'
import moment from "moment";


const TravelList = (props) => {
    let {_adminGetTravelList} = props

    useEffect(() => {
        _adminGetTravelList({
            page: 1,
            limit: 10
        })
    }, [_adminGetTravelList])


    const columns = [
        {
            key:"no",
            title:"No",
            width:50,
            render:(_,val,index)=> index + 1
        },
        {
            dataIndex: 'photo',
            key: 'id',
            title:"Thumbnail",
            width:320,
            render: (val, _) => {
                let thumbnail = ''
                if (Array.isArray(val) && val.length > 0) {
                    thumbnail = [val[0]?.prefix, val[0]?.url].join("")
                }
                return (
                    <Flex alignItems={'center'} mobileFlex={true}>
                        <AvatarCustom thumbnail={thumbnail} groups={val}/>
                        <span className={'ml-2'}>{_?.name ?? _?.travel_name ?? "-"}</span>
                    </Flex>
                )
            }
        },
        {
            dataIndex: "slug",
            key: "slug",
            title:"Slug",
        },
        {
            dataIndex: "price",
            key: "price",
            width:120,
            title:"Price",
            render: (_,val,index)=> {
                return (
                    <div className="w-full">
                        {
                            typeof(val?.is_free) !== "undefined" && val?.is_free === true ? (
                                <Badge color={'cyan'} text={'FREE'}/>
                            ):(
                                <Badge color={'yellow'} text={`${_?.regular ?? 0}`}/>
                            )
                        }
                    </div>
                )
            }
        },
        {
            dataIndex: "periods",
            key: "periods",
            width:120,
            title:"Periods",
            render: (_,val,index)=> {
                return (
                    <div className="w-full">
                        <Badge color={'cyan'} text={`${_?.length ?? 0} Periods`}/>
                    </div>
                )
            }
        },
        {
            dataIndex: "categories",
            key: "categories",
            title:"Categories",
            render: (_,val,index)=> {
                return (
                    <div className="w-full flex flex-col">
                        {
                            typeof(_) !== "undefined" && Array.isArray(_) && _?.length > 0 && (
                                _?.map((item)=> (
                                    <Badge color={item?.background ?? "yellow"} text={item?.name ?? "-"}/>
                                ))
                            )
                        }
                    </div>
                )
            }
        },
        {
            key:"action",
            title:"",
            width:80,
            render:(_)=> {
                return (
                    <div>
                        <div className="d-none d-md-flex">
                            <div className="text-right d-flex">
                                <Link to={`/app/travel/p/${_?._id}`}>
                                    <Badge count={_?.seen ?? 0} showZero>
                                        <Button
                                            type={'primary'}
                                            size={'small'}
                                            ghost
                                            icon={<EyeOutlined/>}
                                        />
                                    </Badge>
                                </Link>
                                <Link to={`/app/travel/update/${_?.slug}`}>
                                    <Button
                                        className={'ml-3'}
                                        type={'primary'}
                                        size={'small'}
                                        ghost
                                        icon={<EditOutlined />}
                                    />
                                </Link>
                            </div>
                        </div>
                        <div className="d-md-none">

                        </div>
                    </div>
                )
            }
        }
    ]

    const expandable = {
        expandedRowRender: (_,val) => {
            return (
                <div>
                    <p style={{ margin: 0 }}>{_?.bio ?? "-"}</p>

                    <Card title={'Periods'}>
                        <Table
                            pagination={false}
                            columns={[
                                {
                                    dataIndex:"_id",
                                    key:"_id",
                                    title:"No",
                                    width:50,
                                    render: (_,val,index)=> index + 1
                                },
                                {
                                    dataIndex:"open_time",
                                    key:"open_time",
                                    title:"Open",
                                    render: (_,val,index)=> {
                                        return moment(_).format('HH:mm a')
                                    }
                                },
                                {
                                    dataIndex:"close_time",
                                    key:"close_time",
                                    title:"Close",
                                    render: (_,val,index)=> {
                                        return moment(_).format('HH:mm a')
                                    }
                                },
                                {
                                    dataIndex:"day",
                                    key:"day",
                                    title:"Day",
                                    render: (_,val,index)=> _
                                },

                            ]}
                            dataSource={_?.periods ?? []}
                        />
                    </Card>
                </div>
            )
        },
        rowExpandable: record => record?._id !== 'Not Expandable',
    }

    return (
        <Card bordered={false} title={'Travel List'}>
            <Table
                expandable={expandable}
                rowKey={'_id'}
                loading={props?.loading ?? false}
                columns={columns}
                dataSource={props?.data ?? {}}
            />
        </Card>
    )
}

TravelList.propTypes = {
    _adminGetTravelList: PropTypes.func.isRequired,
}
TravelList.defaultProps = {
    _adminGetTravelList: (query = {}) => {
        console.log(query)
    }
}
export default connect(({Travel}) => {
    let {admin} = Travel
    let {list} = admin
    return {...list}
}, {_adminGetTravelList})(TravelList)
